.forecast-container{
    background-color: rgba(0, 0, 0, .75);
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.forecast-slider {
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.forecast-card {
    background-color: rgba(0, 0, 0, .4);
    border: 1px solid white;
    border-radius: 1rem;
    width: 10rem;
    height: 12rem;
    padding: .5rem;
    overflow: hidden; /*revisar*/
    display: grid;
    grid-template-rows: 20% 80%;
    flex-shrink: 0;
}
.forecast-card--day{
    text-align: center;
}
.forecast-card--details {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.forecast-card--details-icon {
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.forecast-card--details-icon img {
    width: 4.5rem;
}
.forecast-card--details-icon p {
    font-size: 1.25rem;
}
.forecast-card--details-info p {
    text-align: center;
}

@media (max-width: 390px) {
    .forecast-container {
        margin-top: 1rem;
        width: 100vw;
        overflow: scroll;
        padding: 1rem;
        justify-content: flex-start;
    }
    .forecast-slider {
        display: flex;
        gap: 1.5rem;
        width: auto;
    }
}
@media (max-width: 850px){
    .forecast-container {
        margin-top: 1rem;
        width: 100vw;
        overflow: scroll;
        padding: 1rem;
        justify-content: flex-start;
    }
    .forecast-slider {
        display: flex;
        gap: 3rem;
        width: auto;
    }
}