@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;700&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-image: url('https://images.unsplash.com/photo-1460186136353-977e9d6085a1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80');
  background-size: cover;
  background-repeat: no-repeat;
  color: wheat;
  
}

.App {
  display: grid;
  height: 100vh;
  grid-template-rows: 7% 58% 35%;
}

@media (max-width: 390px){
  .App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: auto;
    /* width: 100%; */
  }
}
@media (max-width: 850px){
  .App{
    /* display: flex;
    flex-direction: column; */
    grid-template-rows: 7% 63% 30%;
  }
}